<template>
  <div class="py-2 white--text key-container text-subtitle-1">
    <!-- <v-row>
      <v-col cols="6" class="text-right">App License</v-col>
      <v-col cols="6">
        <v-btn small color="#3F3D62" class="px-2 flex-grow-1 text-left text-none">Unlicensed</v-btn>
      </v-col>
    </v-row> -->
    <v-row class="my-0">
      <v-col cols="6" class="text-right">OpenAI API Key</v-col>
      <v-col cols="6">
        <v-btn small color="#3F3D62" class="px-2 flex-grow-1 text-left text-none" @click="open">
          <span v-if="openAIKey">
            <v-icon small color="success">mdi-check</v-icon>
            {{ obfuscate(openAIKey) }}
          </span>
          <span v-else>Enter API Key</span>
        </v-btn>
      </v-col>
    </v-row>
    <!-- <v-divider class="my-2"></v-divider>
    <div class="text-center text-subtitle-2 pb-2">Enhanced ChatGPT - 2023</div> -->

    <!-- OpenAI Key modal -->
    <v-dialog v-model="dialog" persistent max-width="350">
      <v-card>
        <v-card-title class="px-6 py-4 d-flex">
          <div class="text-h6 text2--text font-weight-bold flex-grow-1">Enter OpenAI API Key</div>
          <v-btn class="close-btn" icon @click="close"><v-icon>mdi-close</v-icon></v-btn>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text class="pt-6">
          <v-form ref="form" v-model="validForm" @submit.prevent="">
            <div class="text-body-2 mb-3">Your API Key is stored locally on your browser and never sent anywhere else.</div>
            <v-text-field v-model="openAIKeyParam" :rules="[keyRules]" autofocus dense outlined placeholder="Enter API Key" color="#5E5C8D" />
            <div class="text-center">
              <a href="https://platform.openai.com/account/api-keys" target="_blank">Get API key from OpenAI dashboard</a>
            </div>
            <div v-if="error" class="error--text text-right caption">
              {{ error }}
            </div>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-row class="ma-0 pb-6">
            <v-col>
              <v-btn class="add-btn white--text" color="#5E5C8D" block :disabled="!validForm || sendingForm" :loading="sendingForm" @click="save">
                Save
              </v-btn>
            </v-col>
          </v-row>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  name: "ChatList",
  props: {},
  data: () => ({
    dialog: false,
    validForm: true,
    sendingForm: false,
    error: "",
    openAIKeyParam: "",
  }),
  computed: {
    ...mapGetters(["openAIKey"]),
  },
  methods: {
    ...mapActions(["saveOpenAIKey"]),
    close() {
      this.dialog = false;
      this.openAIKeyParam = "";
      this.error = "";
    },
    open() {
      this.dialog = true;
      this.openAIKeyParam = this.openAIKey;
    },
    keyRules(value) {
      if (value.length === 0) {
        return "Required";
      }
      return true;
    },
    async save() {
      try {
        this.sendingForm = true;
        await this.saveOpenAIKey({ key: this.openAIKeyParam });
        this.close();
      } catch (error) {
        this.error = error.message;
      } finally {
        this.sendingForm = false;
      }
    },
    obfuscate(key) {
      return `${key.slice(0, 4)}***${key.slice(-4)}`;
    },
  },
};
</script>

<style lang="scss" scoped>
@import "KeySetting.scss";
</style>
