<template>
  <div>
    <v-list class="chat-list">
      <v-list-item v-for="(msg, i) in currentChat.messages" :key="i" class="my-4 pa-0">
        <div v-if="msg.role === 'user'" class="d-flex justify-end" style="width: 100%">
          <div class="user-text pa-3 mr-4 text-body-2 text-pre-wrap rounded elevation-3">
            {{ msg.content }}
          </div>
          <v-avatar color="blue" size="36">
            <v-icon size="30" dark>mdi-account</v-icon>
          </v-avatar>
        </div>
        <div v-else class="d-flex">
          <v-avatar color="warning" size="36">
            <v-icon size="24" dark>mdi-robot</v-icon>
          </v-avatar>
          <div class="assistant-text pa-3 ml-4 text-body-2 text-pre-wrap rounded elevation-3">
            {{ msg.content }}
          </div>
        </div>
      </v-list-item>
      <!-- loading icon -->
      <v-list-item v-if="sendingMessage" class="my-4 pa-0">
        <v-avatar color="warning" size="36">
          <v-icon size="24" dark>mdi-robot</v-icon>
        </v-avatar>
        <BeatLoader color="#86A0FF" size="12px" class="ml-3"></BeatLoader>
      </v-list-item>
    </v-list>

    <!-- CHAT PROMPT -->
    <div v-if="error" class="error--text text-right caption">
      {{ error }}
    </div>
    <div class="d-flex" :class="$vuetify.breakpoint.smAndDown ? 'sm-prompt-field' : 'lg-prompt-field'">
      <v-textarea
        v-model="message"
        block
        placeholder="Type something to send..."
        flat
        light
        solo
        hide-details
        rows="1"
        auto-grow
        @keydown.enter="sendMessage"
      >
        <template #append>
          <v-btn large icon :disabled="sendingMessage || message === ''" :loading="sendingMessage" color="#6e6996" @click="sendMessage">
            <v-icon>mdi-send</v-icon>
          </v-btn>
        </template>
      </v-textarea>
    </div>
  </div>
</template>

<script>
import BeatLoader from "vue-spinner/src/BeatLoader";
import { mapActions, mapGetters, mapMutations } from "vuex";

export default {
  name: "ChatList",
  components: { BeatLoader },
  props: {},
  data: () => ({
    message: "",
    error: "",
    sendingMessage: false,
  }),
  computed: {
    ...mapGetters(["currentChat"]),
  },
  created() {},
  methods: {
    ...mapMutations(["setCurrentChatId", "addMessageToChat", "addNewChat"]),
    ...mapActions(["submitPrompt"]),
    async sendMessage(event) {
      // if command + enter then allow
      // if command + enter then allow
      if (this.sendingMessage) return;
      if (event && event.keyCode === 13 && !event.metaKey) return;
      try {
        this.error = "";
        // create a chat if it doesn't exist
        if (!this.currentChat.id) {
          const newId = Math.random().toString(36).substring(2, 15) + Math.random().toString(36).substring(2, 15);
          this.addNewChat({ id: newId, title: "Chat with ChatGPT", question: this.message });
          this.setCurrentChatId(newId);
        }
        this.addMessageToChat({ chatId: this.currentChat.id, message: { role: "user", content: this.message } });
        this.message = "";
        this.$vuetify.goTo(document.body.scrollHeight);

        // send prompt to server
        this.sendingMessage = true;
        const response = await this.submitPrompt({ messages: this.currentChat.messages });
        this.addMessageToChat({ chatId: this.currentChat.id, message: this.parseResponse(response) });
      } catch (e) {
        this.error = e.message;
      } finally {
        this.sendingMessage = false;
      }
    },
    parseResponse(response) {
      const { content } = response;
      // remove the first "\n\n" and the last "\n\n" if they exist
      const newContent = content.replace(/^\n\n/, "").replace(/\n\n$/, "").trim();
      return { ...response, content: newContent };
    },
  },
};
</script>

<style lang="scss" scoped>
@import "ChatContent.scss";
</style>
