<template>
  <div>
    <div class="d-flex px-4 pt-3 pb-2 align-center">
      <img alt="Logo" src="@/assets/logo.png" height="36" />
      <div class="white--text ml-3">
        <div class="text-body-1 font-weight-medium">ChatGPT</div>
        <div class="text-caption">Enhanced UI</div>
      </div>
    </div>
    <v-divider></v-divider>
    <div style="background-color: #3f3d62">
      <v-text-field
        v-model="search"
        block
        placeholder="Search"
        solo
        dark
        flat
        height="55"
        hide-details
        background-color="#3f3d62"
        prepend-inner-icon="mdi-magnify"
      >
        <template #append-outer>
          <v-btn icon class="mr-2" @click="createNewChat"><v-icon>mdi-plus</v-icon></v-btn>
        </template>
      </v-text-field>
    </div>
    <v-divider></v-divider>
    <v-list class="mt-2">
      <v-list-item v-if="chats.length === 0">
        <v-list-item-content>
          <v-list-item-title class="text-center">No chats found</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
      <v-list-item-group v-model="selectedChatId">
        <v-list-item v-for="chat in chats" :key="chat.id" link @click="saveCurrentChat(chat.id)">
          <v-list-item-icon>
            <v-avatar :color="chatColor(chat.id)" class="lighten-3" size="40">
              <v-icon size="23" dark>mdi-forum-outline</v-icon>
            </v-avatar>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ chat.title }}</v-list-item-title>
            <v-list-item-subtitle>{{ chat.question }}</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-list-item-group>
    </v-list>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

const COLORS = ["#0364FF", "#3CDBD1", "#FBC94A", "#F1939F", "#A693F1", "#93B8F1", "#93E0F1", "#EFF193", "#F1A993"];

export default {
  name: "ChatList",
  props: {},
  data: () => ({
    selectedChatId: null,
    search: "",
  }),
  computed: {
    ...mapGetters(["currentChatId", "currentChat", "chatList"]),
    chats() {
      return Object.values(this.chatList).filter((chat) => chat.title.toLowerCase().includes(this.search.toLowerCase()));
    },
  },
  watch: {
    selectedChatId() {
      if (this.selectedChatId == null) this.setCurrentChatId(null);
    },
    currentChatId() {
      this.selectedChatId = this.currentChatId ? this.chats.findIndex((chat) => chat.id === this.currentChat.id) : null;
    },
  },
  created() {
    this.selectedChatId = this.currentChat.id ? this.chats.findIndex((chat) => chat.id === this.currentChat.id) : null;
  },
  methods: {
    ...mapMutations(["setCurrentChatId"]),
    saveCurrentChat(chatId) {
      this.setCurrentChatId(chatId);
    },
    createNewChat() {
      this.selectedChat = null;
      this.setCurrentChatId(null);
    },
    chatColor(chatId) {
      // return deterministic color based on chatId that take into account all characters of the chatId
      let colorIndex = 0;
      for (let i = 0; i < chatId.length; i += 1) {
        colorIndex += chatId.charCodeAt(i);
      }
      return COLORS[colorIndex % COLORS.length];
    },
  },
};
</script>

<style lang="scss" scoped>
@import "ChatList.scss";
</style>
